.container {
  margin: 3rem 0;
}

.card {
  margin: 0 1rem !important;
}

.infected {
  border-bottom: 1rem solid rgba(0, 0, 255, 0.5);
}

.recovered {
  border-bottom: 1rem solid rgba(0, 255, 0, 0.5);
}

.deaths {
  border-bottom: 1rem solid rgba(255, 0, 0, 0.5);
}

@media (max-width: 770px) {
  .card {
    margin: .5rem 0 !important;
  }
}
